import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { Subject, Subscription } from 'rxjs';

import { Chart, Plugin } from 'chart.js';
import { DelayService, UserPreferencesService } from 'shared-code-lib';
import { ItemActionHandler } from '../../../blocks/core/itemActionHandler';
import { FilterChangedEvent } from '../../../blocks/filters/FilterChangedEvent';
import { IFilter } from '../../../blocks/models/filter';
import {
  ActivityWidget,
  ChartWidget,
  DonutWidget,
  FileDropWidget,
  HomePageTab,
  ListViewWidget,
  MeterWidget,
  QuickDropWidget,
  TabItem,
  Widget,
  WidgetType,
} from '../../../blocks/models/home-page-tab';
import { ActionService } from '../../../blocks/services/action.service';
import { Action, SpinnerState } from '../../../blocks/services/models';

@Component({
  selector: 'app-tab-item',
  templateUrl: './tab-item.component.html',
  styleUrls: ['./tab-item.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TabItemComponent implements OnInit, OnChanges, OnDestroy {
  @Output() addWidgets = new EventEmitter();
  @Output() saveChanges = new EventEmitter();
  @Output() toggleSpinner = new EventEmitter<SpinnerState>();
  @Output() reloadWidgets = new EventEmitter();
  @Output() widgetMaximized = new EventEmitter<void>();
  @Output() widgetRestored = new EventEmitter<void>();
  @Input() tab: HomePageTab;
  @Input() isCurrent: boolean;
  @Input() widgets: Array<Widget>;
  @Input() scrollbar: PerfectScrollbarDirective;
  @Input() editMode: boolean;

  filterEvents = {};

  itemActionHandler: ItemActionHandler;

  actionFolderIds = {};

  widgetResizingSubject = new Subject<Widget>();
  widgetResizedSubject = new Subject<Widget>();
  widgetResizingObservable = this.widgetResizingSubject.asObservable();
  chartsArray: Array<any> = [];
  meterCenterText: Plugin;
  donutCenterText: Plugin;
  barsCenterText: Plugin;

  isMaximized: boolean;
  maximizedWidgetId: string;
  maximizePreferenceKey: string;

  WidgetType = WidgetType;

  subscriptions = new Subscription();

  constructor(
    private userPreferencesService: UserPreferencesService,
    private delayService: DelayService,
    private actionService: ActionService
  ) {}

  ngOnInit() {
    this.itemActionHandler = this.actionService.createItemActionHandler();

    this.tab.items = this.tab.items.filter((item) => {
      return this.widgets.find((w) => w.id === item.widgetId);
    });
    this.meterCenterText = {
      id: 'meterCenterText',
      afterDatasetsDraw: (chart) => {
        if (!chart.config.options.plugins['meterCenterText']) {
          return;
        }

        const ctx = chart.ctx;
        const txt1 = chart.config.options.plugins['meterCenterText'].txt1 ?? '';
        const txt2 = chart.config.options.plugins['meterCenterText'].txt2 ?? '';

        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';
        const centerX = (chart.chartArea.left + chart.chartArea.right) / 2;

        ctx.fillStyle = '#c1c1c1';
        ctx.font = 'bold 12px Arial';
        ctx.fillText(txt1, centerX, chart.chartArea.bottom - 6);

        ctx.font = 'bold 40px Arial';
        ctx.fillStyle = '#151515';

        ctx.fillText(txt2, centerX, chart.chartArea.bottom - 32);
      },
    };

    this.donutCenterText = {
      id: 'donutCenterText',
      afterDatasetsDraw: (chart) => {
        if (!chart.config.options.plugins['donutCenterText']) {
          return;
        }

        const ctx = chart.ctx;
        const txt1 = chart.config.options.plugins['donutCenterText'].txt1 ?? '';
        const txt2 = chart.config.options.plugins['donutCenterText'].txt2 ?? '';

        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';
        const centerX = (chart.chartArea.left + chart.chartArea.right) / 2;
        const centerY = (chart.chartArea.top + chart.chartArea.bottom) / 2;

        ctx.fillStyle = '#151515';
        const fontSizeToUse1 = 12;
        ctx.font = fontSizeToUse1 + 'px Arial';
        ctx.fillText(txt1, centerX, centerY - 15);

        const fontSizeToUse = 40;
        ctx.font = fontSizeToUse + 'px Arial';
        ctx.fillStyle = '#151515';

        ctx.fillText(txt2, centerX, centerY + 15);
      },
    };

    this.barsCenterText = {
      id: 'barsCenterText',
      afterDatasetsDraw: (chart) => {
        if (!chart.config.options.plugins['barsCenterText']) {
          return;
        }

        const ctx = chart.ctx;
        const txt1 = chart.config.options.plugins['barsCenterText'].txt1 ?? '';
        const txt2 = chart.config.options.plugins['barsCenterText'].txt2 ?? '';

        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';
        const centerX = (chart.chartArea.left + chart.chartArea.right) / 2;
        const centerY = (chart.chartArea.top + chart.chartArea.bottom) / 2;

        ctx.fillStyle = '#151515';
        const fontSizeToUse1 = 12;
        ctx.font = fontSizeToUse1 + 'px Arial';
        ctx.fillText(txt1, centerX, centerY - 15);

        const fontSizeToUse = 40;
        ctx.font = fontSizeToUse + 'px Arial';
        ctx.fillStyle = '#151515';

        ctx.fillText(txt2, centerX, centerY + 15);
      },
    };

    this.chartsArray = [this.meterCenterText, this.donutCenterText, this.barsCenterText];
    const plugins = Chart.registry.plugins;

    this.chartsArray.forEach((chart) => {
      if (!plugins.get(chart.id)) {
        Chart.registry.addPlugins(chart);
      }
    });

    this.checkMaximizedWidget();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.editMode) this.checkMaximizedWidget();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  checkMaximizedWidget = (): void => {
    this.maximizePreferenceKey = `maximized-widget-tab-${this.tab.id}`;
    this.maximizedWidgetId = this.userPreferencesService.getPreference(this.maximizePreferenceKey);

    if (!this.maximizedWidgetId) return;
    if (this.tab.items.findIndex((x) => this.getWidget(x)?.id == this.maximizedWidgetId) != -1) return;

    this.maximizedWidgetId = '';
    this.userPreferencesService.setPreference(this.maximizePreferenceKey, '');
  };

  getWidget = (tabItem: TabItem): Widget => {
    const widget = this.widgets.find((w) => w.id === tabItem.widgetId);
    if (widget) {
      if ((widget as ListViewWidget).filters) {
        (widget as ListViewWidget).filters.forEach((filter) => {
          const filterPreferenceKey = `widget-filter-${filter.id.substring(filter.id.lastIndexOf('.') + 1)}-${
            tabItem.itemId
          }`;
          const filterPreferenceValue = this.userPreferencesService.getPreference(filterPreferenceKey);
          if (filterPreferenceValue) filter.value = filterPreferenceValue;
        });
      }

      return widget;
    }

    return new Widget(null);
  };

  onAddWidgetsClick() {
    this.addWidgets.emit();
  }

  updateGridLayout = (height: number, tab: HomePageTab, item: TabItem, index: number): void => {
    item.height = height;
    tab.items[index] = item;
    this.widgetResizingSubject.next(this.getWidget(item));
    this.saveChanges.emit();
  };

  onResized = (item: TabItem): void => {
    this.widgetResizedSubject.next(this.getWidget(item));
  };

  removeWidget(index: number, row: number, primaryColumn: number) {
    const widget = this.getWidget(this.tab.items[index]);
    if (widget && widget.id == this.maximizedWidgetId) {
      this.maximizedWidgetId = '';
      this.widgetRestored.next();
    }

    this.tab.items.splice(index, 1);

    const otherItemsExist = this.tab.items.some((item) => item.row === row && item.primaryColumn === primaryColumn);
    if (!otherItemsExist) {
      this.tab.items
        .filter((item) => item.row > row && item.primaryColumn === primaryColumn)
        .forEach((item) => {
          item.row -= 1;
        });
    }

    this.tab.items = this.tab.items.slice();
  }

  onSort() {
    this.saveChanges.emit();
  }

  onToolbarAction = (tabItem: TabItem, action: Action): void => {
    console.log(`action: ${action.name}`);

    this.itemActionHandler.invoke(this.actionFolderIds[tabItem.id], action, [], []);
  };

  onActionData = (tabItem: TabItem, folderId: string): void => {
    console.log(`action-data: ${folderId}`);

    this.actionFolderIds[tabItem.id] = folderId;
  };

  getFilterChangedEventFor(item: TabItem) {
    const property = item.id.toString();

    if (this.filterEvents[property] === undefined) {
      this.filterEvents[property] = new EventEmitter<FilterChangedEvent>();
    }

    return this.filterEvents[property];
  }

  onFilterChanged(value: string, filter: IFilter, item: TabItem): void {
    this.getFilterChangedEventFor(item).next(new FilterChangedEvent(filter, value));
    const filterPreferenceKey = `widget-filter-${filter.id.substring(filter.id.lastIndexOf('.') + 1)}-${item.itemId}`;
    this.userPreferencesService.setPreference(filterPreferenceKey, value);
  }

  toggleMaximizeState = (widget: Widget): void => {
    if (this.maximizedWidgetId == widget.id) {
      this.maximizedWidgetId = '';
      this.widgetRestored.next();
    } else {
      this.maximizedWidgetId = widget.id;
      this.widgetMaximized.next();
    }

    this.scrollbar.update();

    this.userPreferencesService.setPreference(this.maximizePreferenceKey, this.maximizedWidgetId);
    this.delayService.execute(() => this.widgetResizedSubject.next(widget), 200);
  };

  getFileDropWidget = (item: TabItem) => this.getWidget(item) as FileDropWidget;
  getListViewWidget = (item: TabItem) => this.getWidget(item) as ListViewWidget;
  getMeterWidget = (item: TabItem) => this.getWidget(item) as MeterWidget;
  getQuickDropWidget = (item: TabItem) => this.getWidget(item) as QuickDropWidget;
  getDonutWidget = (item: TabItem) => this.getWidget(item) as DonutWidget;
  getActivityWidget = (item: TabItem) => this.getWidget(item) as ActivityWidget;
  getChartWidget = (item: TabItem) => this.getWidget(item) as ChartWidget;
}
