import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { FileSystemDirectoryEntry, FileSystemFileEntry, NgxFileDropEntry } from 'ngx-file-drop';
import { NgxSmartModalService } from 'ngx-smart-modal';

import { MetaDataCollection } from 'shared-code-lib';
import { Filename } from '../../../../blocks/core/fileName';
import { FileDropWidget } from '../../../../blocks/models/home-page-tab';
import { SpinnerState } from '../../../../blocks/services/models';
import { FileDropService } from './file-drop.service';

@Component({
  selector: 'app-file-drop',
  templateUrl: './file-drop.component.html',
  styleUrls: ['./file-drop.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FileDropComponent {
  @Input() widget: FileDropWidget;
  @Input() icon: string;
  @Output() toggleSpinner = new EventEmitter<SpinnerState>();
  filesToUpload: Array<File> = [];
  filesToSubmit: Array<File> = [];

  readonly supportedFileTypes = Filename.supportedFileTypes;

  constructor(private ngxSmartModalService: NgxSmartModalService, private fileDropService: FileDropService) {}

  dropped(files: NgxFileDropEntry[]): void {
    let filesCount = files.length;
    for (const droppedFile of files) {
      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.filesToUpload.push(file);
          filesCount -= 1;

          if (!filesCount) {
            this.checkWidgetPrompts([...this.filesToUpload]);
            this.filesToUpload = [];
          }
        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
      }
    }
  }

  onFileSelected = (event: any): void => {
    if (!event.target.files || !event.target.files.length) {
      return;
    }

    for (const file of event.target.files) {
      this.filesToUpload.push(file);
    }

    this.checkWidgetPrompts([...this.filesToUpload]);
    this.filesToUpload = [];
    event.target.value = null;
  };

  checkWidgetPrompts(files: Array<File>) {
    this.filesToSubmit = files;

    if (this.widget.prompts && this.widget.prompts.length > 0) {
      this.showWorkflowPromptsModal();
      return;
    }

    this.submitFiles();
  }

  showWorkflowPromptsModal = () => {
    this.ngxSmartModalService.setModalData(
      {
        prompts: this.widget.prompts,
        widget: this.widget,
        filesToSubmit: this.filesToSubmit,
        toggleSpinner: this.toggleSpinner,
      },
      'workflowPromptsModal',
      true
    );

    this.ngxSmartModalService.getModal('workflowPromptsModal').open();
  };

  submitFiles() {
    this.fileDropService.submitFilesToWidget(
      this.widget,
      this.filesToSubmit,
      new MetaDataCollection(),
      this.toggleSpinner
    );
  }
}
