import { ChangeDetectorRef, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import { ChartRequest, DelayService } from 'shared-code-lib';
import { ItemActionHandler } from '../../../../blocks/core/itemActionHandler';
import { FilterChangedEvent } from '../../../../blocks/filters/FilterChangedEvent';
import { IFilterValue } from '../../../../blocks/models/filter';
import {
  ChartWidget,
  IChartData,
  ListViewBarChartData,
  ListViewChartData,
  Widget,
} from '../../../../blocks/models/home-page-tab';
import { ActionService } from '../../../../blocks/services/action.service';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';
import { ApiResult, SpinnerState } from '../../../../blocks/services/models';
import { WidgetsService } from '../../../../blocks/services/widgets.service';

@Component({
  selector: 'app-chart-widget',
  templateUrl: './chart-widget.component.html',
  styleUrls: ['./chart-widget.component.scss'],
})
export class ChartWidgetComponent implements OnInit, OnDestroy {
  @Input() widget: ChartWidget;
  @Input() onResizeNotifier: Subject<Widget>;
  @Input() filterChanged: EventEmitter<FilterChangedEvent>;
  @ViewChild(PerfectScrollbarDirective) directiveRef?: PerfectScrollbarDirective;

  @Output() actionData = new Subject<string>();
  reloadData = new Subject<Array<IChartData>>();
  graphResize = new Subject<number>();

  chartData: Array<IChartData>;
  spinnerMessage: string;
  itemActionHandler: ItemActionHandler;
  subscriptions = new Subscription();

  sort: SortDescriptor;

  loadItemCount = 25;
  loading = true;
  loadingFailed: boolean;
  errors: Array<string>;
  filters: Array<IFilterValue> = [];
  chartHeight: number;

  constructor(
    private delay: DelayService,
    private translate: TranslateService,
    private toastr: ToastrService,
    private widgetsService: WidgetsService,
    private hydraApi: HydraApiService,
    private actionService: ActionService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.subscriptions.add(this.filterChanged.subscribe(this.onFilterChanged));

    this.itemActionHandler = this.actionService.createItemActionHandler();

    this.subscriptions.add(this.itemActionHandler.toggleLoading.subscribe(this.toggleLoadingState));

    this.subscriptions.add(
      this.widgetsService.onReloadData.subscribe((relatedWidget: string) => {
        if (relatedWidget === this.widget.relatedWidgetTopic) {
          this.loadChartData();
        }
      })
    );

    this.initializeData();
  }

  initializeData = (): void => {
    this.addWidgetFilters();
    this.loadChartData();
  };

  onFilterChanged = (fce: FilterChangedEvent): void => {
    this.addFilter({ id: fce.filter.id, label: fce.filter.label, value: fce.value });
    this.loadChartData();
  };

  addWidgetFilters = (): void => {
    if (!this.widget?.filters?.length) return;

    this.widget.filters.forEach((x) => {
      this.addFilter({
        id: x.id,
        label: x.label,
        value: x.value,
      });
    });
  };

  addFilter(filterValue: IFilterValue): void {
    const existingFilterIndex = this.filters.findIndex((f) => f.id === filterValue.id);
    if (existingFilterIndex > -1) {
      this.filters[existingFilterIndex] = filterValue;
    } else {
      this.filters.push(filterValue);
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  onSelectBar = (filters: Array<IFilterValue>): void => {
    console.log('onSelectBar', filters);
    if (this.chartData.length == 0) return;
  };

  toggleLoadingState = (state: SpinnerState): void => {
    this.delay.execute(() => {
      this.loading = state.state;
      this.spinnerMessage = state.message ? state.message : this.translate.instant('Common.Loading');
    }, 0);
  };

  loadChartDataEx(): Promise<void> {
    let chartResquest = new ChartRequest();
    chartResquest.filters = this.filters;
    return this.hydraApi.getWidgetChartData(
      this.widget.id,
      chartResquest,
      this.chartDataLoaded,
      this.chartDataLoadFailed
    );
  }

  loadChartData = (): void => {
    this.toggleLoadingState({ state: true });
    this.hideSpinner([this.loadChartDataEx()]);
  };

  chartDataLoaded = (result: ApiResult): void => {
    if (result.success) {
      this.setChartData((result.object as ListViewBarChartData).data);
      return;
    }

    this.loadingFailed = true;
    this.errors = result.errors;
  };

  setChartData = (data: Array<IChartData>): void => {
    if (this.chartData) this.reloadData.next(data);
    this.chartData = data;
    this.cdr.detectChanges();
  };

  chartDataLoadFailed = (err: any): void => {
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
  };

  hideSpinner(promises: Array<Promise<void>>): void {
    Promise.all(promises)
      .then(() => this.toggleLoadingState({ state: false }))
      .catch(() => this.toggleLoadingState({ state: false }));
  }

  getListViewChartData = () => this.chartData as ListViewChartData[];
}
