import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { GridComponent } from '@progress/kendo-angular-grid';
import { SortDescriptor } from '@progress/kendo-data-query';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import {
  ChartRequest,
  Crumb,
  CurrentSelectedDocumentService,
  ICombinedColumn,
  InvokeAction,
  ItemTableDataSource,
  ItemsRequest,
  ItemsResult,
  Pagination,
  SortDirection,
  TableItem,
  UserPreferencesService,
} from 'shared-code-lib';
import { AutoExecuteResult, ItemActionHandler, UpdateResults } from '../../../../blocks/core/itemActionHandler';
import { FilterChangedEvent } from '../../../../blocks/filters/FilterChangedEvent';
import { IFilterValue } from '../../../../blocks/models/filter';
import {
  DonutWidgetData,
  IChartData,
  ListView,
  ListViewBarChartData,
  ListViewChartData,
  ListViewDonutChartData,
  ListViewMode,
  ListViewWidget,
  Widget,
} from '../../../../blocks/models/home-page-tab';
import { ActionService } from '../../../../blocks/services/action.service';
import { ApiService } from '../../../../blocks/services/api.service';
import { AutoExecuteActionsService } from '../../../../blocks/services/auto-execute-actions.service';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';
import { ActionTypes, ApiResult, IAction, IFolder, SpinnerState } from '../../../../blocks/services/models';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import { UtilityService } from '../../../../blocks/services/utility.service';
import { WidgetsService } from '../../../../blocks/services/widgets.service';
import { PreviewDocument } from '../../../../views/product-item/document-preview/document-preview.component';

@Component({
  selector: 'app-list-view',
  templateUrl: './list-view.component.html',
  styleUrls: ['./list-view.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ListViewComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() widget: ListViewWidget;
  @Input() tabItemId: string;
  @Input() tabItemHeight: number;
  @Input() onResizeNotifier: Subject<Widget>;
  @Input() filterChanged: EventEmitter<FilterChangedEvent>;

  @Output() actionData = new Subject<string>();
  reloadData = new Subject<Array<IChartData>>();
  graphResize = new Subject<void>();
  @ViewChild('appGraph', { read: ElementRef }) appGraph: ElementRef;
  @ViewChild('listView', { read: ElementRef }) listView: ElementRef;
  graphHeight: string;

  private _previewDocument: PreviewDocument;
  get previewDocument() {
    return this._previewDocument;
  }
  set previewDocument(document: PreviewDocument) {
    this._previewDocument = document;
    this.currentSelectedDocumentService.setCurrentDocument(document);
  }

  datasource: ItemTableDataSource;
  columns: Array<ICombinedColumn>;
  tableItems: Array<TableItem>;
  selectedView: ListView;
  selectedFilterValues: Array<IFilterValue> = [];
  chartData: Array<IChartData>;
  donutWidgetData: DonutWidgetData;
  donutCenterLabel: string;
  selectedViewIndex: number;
  currentPage = 1;
  itemsPageCount = 0;
  itemsTotal = 0;
  pageSize = 5;
  spinnerMessage: string;
  itemActionHandler: ItemActionHandler;
  subscriptions = new Subscription();
  datasourceSubscription: Subscription;
  autoExecuteSubscriptions = new Subscription();

  sort: SortDescriptor;

  loadItemCount = 25;
  loading = true;
  loadingFailed: boolean;
  errors: Array<string>;
  displayGraph: boolean = false;

  crumbs = new Array<Crumb>();

  viewCounts = ['--', '--', '--', '--', '--', '--', '--', '--', '--', '--', '--', '--', '--'];
  filters: Array<IFilterValue> = [];

  private grid: GridComponent;
  loaders = 0;

  selectedItems: TableItem[] = [];
  keepSelectionOnLoad = false;
  currentFolder: IFolder;

  constructor(
    private translate: TranslateService,
    private api: ApiService,
    private toastr: ToastrService,
    private widgetsService: WidgetsService,
    private hydraApi: HydraApiService,
    private userPreferencesService: UserPreferencesService,
    private actionService: ActionService,
    private cdr: ChangeDetectorRef,
    private userStateService: UserStateService,
    private currentSelectedDocumentService: CurrentSelectedDocumentService,
    private autoExecuteActionsService: AutoExecuteActionsService,
    private utility: UtilityService
  ) {}

  ngOnInit() {
    this.spinnerMessage = this.translate.instant('Common.Loading');
    this.subscriptions.add(this.filterChanged.subscribe(this.onFilterChanged));

    this.itemActionHandler = this.actionService.createItemActionHandler();

    this.subscriptions.add(this.itemActionHandler.toggleLoading.subscribe(this.toggleLoadingState));
    this.subscriptions.add(
      this.itemActionHandler.reload.subscribe((keepSelection) => {
        if (keepSelection) this.keepSelectionOnLoad = keepSelection;
        this.onReload();
      })
    );
    this.subscriptions.add(this.itemActionHandler.openFolder.subscribe(this.onOpenFolder));
    this.subscriptions.add(
      this.itemActionHandler.update.subscribe((updateResults: UpdateResults) => {
        if (updateResults.silent) {
          this.silentlyUpdateItems(updateResults);
        } else {
          this.keepSelectionOnLoad = true;
          this.onReload();
        }
      })
    );
    this.subscriptions.add(
      this.onResizeNotifier.subscribe((widget: Widget) => {
        if (widget.id === this.widget.id) {
          this.handleViewingRecords();
        }
      })
    );
    this.subscriptions.add(
      this.widgetsService.onReloadData.subscribe((relatedWidget: string) => {
        if (relatedWidget === this.widget.relatedWidgetTopic) {
          this.onReload();
        }
      })
    );
    this.subscriptions.add(
      this.itemActionHandler.onAutoExecuteResult.subscribe((result: AutoExecuteResult) => {
        this.autoExecuteSubscriptions.add(
          this.autoExecuteActionsService.setupAutoExecuteActions(result, this.currentFolder, this.itemActionHandler)
        );
      })
    );

    this.initializeData();
  }

  ngAfterViewInit(): void {
    if (this.tabItemHeight == this.widget.defaultHeight) {
      this.graphHeight = (this.listView.nativeElement.offsetHeight - 120) as any;
      return;
    }

    this.graphHeight = this.userPreferencesService.getPreference(`widget-${this.widget.id}-chart-height`, 'auto');
  }

  initializeData = (): void => {
    if (this.widget.mode === ListViewMode.Tabs) {
      this.addWidgetFilters();
      this.loadViewCounts();
    } else if (
      this.widget.mode === ListViewMode.HorizontalBarChart ||
      this.widget.mode === ListViewMode.VerticalBarChart
    ) {
      this.addWidgetFilters();

      this.loadChartData();
    } else if (this.widget.mode === ListViewMode.BreadCrumbs) {
      this.loadViewCounts(1);
    } else if (this.widget.mode === ListViewMode.Donut) {
      this.addWidgetFilters();
      this.loadListViewData();
    }
  };

  onFilterChanged = (fce: FilterChangedEvent): void => {
    this.addFilter({ id: fce.filter.id, label: fce.filter.label, value: fce.value });
    this.loadListViewData();
  };

  loadListViewData() {
    const promises = [...this.loadViewCounts(), this.loadItemsEx()];

    if (
      this.widget.mode === ListViewMode.HorizontalBarChart ||
      this.widget.mode === ListViewMode.VerticalBarChart ||
      this.widget.mode === ListViewMode.Donut
    ) {
      promises.push(this.loadChartDataEx());
    }

    this.hideSpinner(promises);
  }

  addWidgetFilters = (): void => {
    if (!this.widget?.filters?.length) return;

    this.widget.filters.forEach((x) => {
      this.addFilter({
        id: x.id,
        label: x.label,
        value: x.value,
      });
    });
  };

  addFilter(filterValue: IFilterValue): void {
    const existingFilterIndex = this.filters.findIndex((f) => f.id === filterValue.id);
    if (existingFilterIndex > -1) {
      this.filters[existingFilterIndex] = filterValue;
    } else {
      this.filters.push(filterValue);
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
    this.autoExecuteSubscriptions.unsubscribe();
    if (this.datasourceSubscription) this.datasourceSubscription.unsubscribe();
  }

  onRender = (grid: GridComponent): void => {
    this.grid = grid;
    this.handleViewingRecords();
  };

  getColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    for (const column of this.selectedView.columns) {
      const defaultPreference = this.selectedView.defaultColumns.find((c) => c.id == column.id);

      if (!defaultPreference) continue;

      columns.push({
        ...column,
        ...{ id: column.id, width: defaultPreference.width, stretch: defaultPreference.stretch },
      });
    }

    return columns;
  }

  initialRowHeight: number = 1;
  handleViewingRecords = (noreload = false): void => {
    if (!this.grid) return;

    let pageSizeIncreased = false;
    const row = this.grid.ariaRoot.nativeElement.querySelectorAll('.k-header')[0];
    if (!row && !this.itemsTotal) return;
    if (row && this.initialRowHeight == 1) this.initialRowHeight = row.offsetHeight;

    const rowHeight = row?.offsetHeight ?? this.initialRowHeight;
    const availableHeight = this.grid.ariaRoot.nativeElement.offsetHeight - rowHeight;
    let maxNumberOfRows = !availableHeight || !rowHeight ? 1 : 0;

    for (
      let maxHeightOfRows = 0;
      availableHeight && rowHeight && availableHeight >= maxHeightOfRows + rowHeight;
      maxHeightOfRows += rowHeight
    ) {
      maxNumberOfRows++;
    }

    pageSizeIncreased = this.pageSize < maxNumberOfRows;
    this.pageSize = maxNumberOfRows;
    let maxNumberOfPages = Math.ceil(this.itemsTotal / this.pageSize);
    if (maxNumberOfPages < this.currentPage && maxNumberOfPages > 0) {
      this.currentPage = maxNumberOfPages;
    }

    if (maxNumberOfRows <= this.tableItems.length) {
      const tis = [];

      for (let row = 0; row < maxNumberOfRows; row++) {
        tis.push(this.tableItems[row]);
      }

      this.tableItems = tis;
      this.selectedItems = this.keepSelectionOnLoad ? this.selectedItems : [];
      this.keepSelectionOnLoad = false;
      this.itemsPageCount = tis.length;

      return;
    }

    if (!noreload && pageSizeIncreased) this.loadItems();
  };

  selectFolder = (folderId: string) => {
    this.currentFolder = this.userStateService.findFolderById(folderId);
    this.selectedItems = [];

    this.loadAutoExecuteActions();
  };

  loadAutoExecuteActions = () => {
    if (!this.currentFolder || !this.currentFolder.actions.length) return;

    let autoExecuteActions = this.currentFolder.actions.filter((a) => (a.type & ActionTypes.AutoExecute) != 0);
    if (!autoExecuteActions.length) return;

    autoExecuteActions.forEach((a) => {
      this.itemActionHandler.invoke(this.currentFolder.id, a, [], []);
    });
  };

  silentlyUpdateItems = (ur: UpdateResults) => {
    if (!ur.silent) return;

    const { tableItems, selectedItems } = this.autoExecuteActionsService.silentlyUpdateItems(
      ur,
      this.tableItems,
      this.selectedItems,
      this.columns
    );

    this.tableItems = tableItems;
    this.selectedItems = selectedItems;
  };

  onSelectTab = (viewIndex: number): void => {
    this.selectedViewIndex = viewIndex;
    this.selectedView = this.widget.views[viewIndex];
    this.currentPage = 0;
    this.itemsPageCount = this.itemsTotal = 0;

    this.selectFolder(this.selectedView.folderId);

    this.cdr.detectChanges();

    this.reloadItems();
  };

  onSelectBar = (filters: Array<IFilterValue>): void => {
    if (this.chartData.length == 0) return;

    this.selectedFilterValues = filters;
    this.currentPage = 0;
    this.itemsPageCount = this.itemsTotal = 0;

    this.reloadItems();
  };

  onOpenFolder = (folderId: string): void => {
    if (!this.selectedView) this.selectedView = this.widget.views[0];

    this.currentPage = 1;
    this.itemsPageCount = this.itemsTotal = 0;
    this.selectedView.folderId = folderId || this.selectedView.folderId;

    this.reloadItems();
  };

  onCrumbClicked = (crumb: Crumb): void => {
    this.onOpenFolder(crumb ? crumb.folderId : null);
  };

  toggleLoadingState = (state: SpinnerState): void => {
    if (state.state) this.loaders++;
    else this.loaders--;
    console.log(this.loaders);
    if (!state.state && this.loaders > 0) return;
    this.loading = state.state;
    this.spinnerMessage = state.message ? state.message : this.translate.instant('Common.Loading');
  };

  private reloadItems() {
    this.loadItems(this.loadItemCount);
    this.loadItemCount = this.pageSize;
  }

  loadChartDataEx(): Promise<void> {
    const request = new ChartRequest();
    request.filters = this.filters;

    return this.hydraApi.getWidgetChartData(this.widget.id, request, this.chartDataLoaded, this.chartDataLoadFailed);
  }

  loadChartData = (): void => this.hideSpinner([this.loadChartDataEx()]);

  chartDataLoaded = (result: ApiResult): void => {
    if (result.success) {
      if (this.widget.mode == ListViewMode.Donut) {
        const donutChartData = result.object as ListViewDonutChartData;

        this.donutWidgetData = { centerCount: donutChartData.centerCount, chunks: donutChartData.chunks };
        this.donutCenterLabel = donutChartData.centerLabel;

        this.setChartData((result.object as ListViewDonutChartData).chunks);
        return;
      }

      this.setChartData((result.object as ListViewBarChartData).data);
      return;
    }

    this.loadingFailed = true;
    this.errors = result.errors;
  };

  setChartData = (data: Array<IChartData>): void => {
    if (this.chartData) this.reloadData.next(data);
    this.chartData = data;
    this.displayGraph = this.chartData ? true : false;
    this.cdr.detectChanges();
    this.handleViewingRecords(true);
  };

  chartDataLoadFailed = (err: any): void => {
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
  };

  loadViewCounts = (i = 0): Array<Promise<void>> => {
    let index = i;
    if (!this.selectedView) this.selectedView = this.widget.views[0];
    const promises = [];
    for (index; index < this.widget.views.length; index++) {
      let view = this.widget.views[index];
      if (!(this.widget.mode === ListViewMode.Tabs || this.widget.mode === ListViewMode.BreadCrumbs)) {
        continue;
      }
      const ds = new ItemTableDataSource(this.widget.productId, view.folderId, this.api);

      const request = new ItemsRequest();

      request.pageNumber = 1;
      request.pageSize = 1;
      request.sortBy = '';
      request.sortByDirection = SortDirection.Ascending;
      request.filters = [...view.filterValues, ...this.filters];

      let viewIndex = index;
      promises.push(
        ds.loadItems(
          request,
          (results) => {
            this.viewCounts[viewIndex] = this.utility.localizeNumber(results.total);
          },
          (err) => {
            console.log(err);
          }
        )
      );
    }

    return promises;
  };

  onDataSourceItemsLoaded = (results: ItemsResult): void => {
    this.crumbs = results.crumbs ? results.crumbs.map((c) => new Crumb(c)).slice() : [];
  };

  createDataSource = (): void => {
    if (!this.selectedView) this.selectedView = this.widget.views[0];

    this.datasource = new ItemTableDataSource(this.widget.productId, this.selectedView.folderId, this.api);
    this.columns = this.getColumns();

    if (this.datasourceSubscription) this.datasourceSubscription.unsubscribe();
    this.datasourceSubscription = this.datasource.onItemsLoaded.subscribe(this.onDataSourceItemsLoaded);

    const defaultSort: SortDescriptor = {
      field: this.selectedView.defaultSortByColumn.columnId,
      dir: this.selectedView.defaultSortByColumn.direction == SortDirection.Ascending ? 'asc' : 'desc',
    };

    this.sort = this.userPreferencesService.getPreference(`widget-sort-${this.selectedView.id}`, defaultSort);
  };

  loadItemsEx = (minNumberToLoad = 0): Promise<void> => {
    this.tableItems = [];
    this.selectedItems = this.keepSelectionOnLoad ? this.selectedItems : [];
    this.createDataSource();

    const request = new ItemsRequest();
    const sortColumn = this.columns.find((c) => c.id === this.sort.field);
    const sortByField = sortColumn ? sortColumn.sortByFieldName : '';
    const sortByDirection = this.sort.dir === 'asc' ? SortDirection.Ascending : SortDirection.Descending;

    if (this.currentPage === 0) this.currentPage = 1;

    request.pageNumber = this.currentPage;
    request.pageSize = Math.max(this.pageSize, minNumberToLoad);
    request.sortBy = sortByField;
    request.sortByDirection = sortByDirection;
    request.filters = this.getFilters();

    this.toggleLoadingState({ state: true });

    this.actionData.next(this.datasource.folder);

    return this.datasource.loadItems(request, (results) => this.itemsLoad(results), this.hideSpinner);
  };

  loadItems = (minNumberToLoad = 0): void => {
    this.hideSpinner([this.loadItemsEx(minNumberToLoad)]);
  };

  getFilters(): Array<IFilterValue> {
    switch (this.widget.mode) {
      case ListViewMode.Tabs:
      case ListViewMode.BreadCrumbs:
        return [...this.selectedView.filterValues, ...this.filters];
      case ListViewMode.HorizontalBarChart:
      case ListViewMode.VerticalBarChart:
      case ListViewMode.Donut:
        return [...this.selectedFilterValues, ...this.filters];
      default:
        return [];
    }
  }

  itemsLoad = (results: ItemsResult): void => {
    this.errors = [];

    this.loadingFailed = !results.success;

    if (results.success) {
      this.tableItems = results.items.map((i) => new TableItem(i, this.columns, this.translate));

      this.selectedItems = this.keepSelectionOnLoad
        ? this.tableItems.filter((i) => this.selectedItems.some((j) => j.data.id === i.data.id))
        : [];
      this.selectedItems.forEach((sI) => (this.tableItems.find((tI) => tI.data.id === sI.data.id).selected = true));

      this.itemsPageCount = results.items.length;
      this.itemsTotal = results.total;
      this.viewCounts[this.selectedViewIndex] = this.utility.localizeNumber(results.total);
      if (this.itemsTotal === 0) this.currentPage = 0;

      this.handleViewingRecords(true);
      this.cdr.markForCheck();
    } else {
      this.errors = results.errors;
      this.viewCounts[this.selectedViewIndex] = '--';
    }

    this.keepSelectionOnLoad = false;
  };

  onLoadPage = (pagination: Pagination): void => {
    this.currentPage = pagination.pageNumber;
    this.loadItems();
  };

  onSort = (sd: SortDescriptor): void => {
    this.sort = sd;
    this.userPreferencesService.setPreference(`widget-sort-${this.selectedView.id}`, this.sort);

    this.loadItems();
  };

  onInvokeAction = (ia: InvokeAction): void => {
    this.itemActionHandler.invoke(this.selectedView.folderId, ia.action, [ia.item], []);
  };

  onReload = (): void => {
    const promises = [...this.loadViewCounts(), this.loadItemsEx()];
    this.hideSpinner(promises);
  };

  onGraphResize = (): void => {
    this.userPreferencesService.setPreference(
      `widget-${this.widget.id}-chart-height`,
      this.appGraph.nativeElement.offsetHeight
    );

    this.onRender(this.grid);

    this.graphResize.next();
  };

  hideSpinner(promises: Array<Promise<void>>): void {
    Promise.all(promises)
      .then(() => this.toggleLoadingState({ state: false }))
      .catch(() => this.toggleLoadingState({ state: false }));
  }

  chunkClicked = (index: number): void => {
    if (!this.chartData[index].filterValues.length) return;

    this.chartData[index].filterValues.forEach((fv) => {
      const filter = this.filters.find((x) => x.id == fv.id);

      this.onFilterChanged(new FilterChangedEvent(filter, fv.value));
    });
  };

  onToolbarActionClicked = (action: IAction) => {
    this.itemActionHandler.invoke(
      this.currentFolder.id,
      action,
      this.selectedItems.map((ti) => ti.data),
      this.currentFolder.filters.map((f) => ({ id: f.id, value: f.value }))
    );
  };

  onTableSelectionChanged = () => {
    this.selectedItems = this.tableItems.filter((ti) => ti.selected);

    if (this.selectedItems.length !== 1) {
      this.previewDocument = null;
      return;
    }

    if (this.previewDocument && this.previewDocument.item.data.id === this.selectedItems[0].data.id) return;

    const selectedItem = this.selectedItems[0];
    if (!selectedItem) return;

    this.previewDocument = selectedItem.data.documentExists
      ? new PreviewDocument(this.selectedItems[0], this.datasource, this.translate)
      : null;

    this.autoExecuteActionsService.setSelectedItem(selectedItem);
  };

  getListViewChartData = () => this.chartData as ListViewChartData[];
}
