import {
  Component,
  ElementRef,
  HostListener,
  Input,
  OnDestroy,
  OnInit,
  Renderer2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';

import { ChartDataset, ChartType } from 'chart.js';
import { DelayService } from 'shared-code-lib';
import { MeterWidget, MeterWidgetData, Widget } from '../../../../blocks/models/home-page-tab';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';
import { WidgetsService } from '../../../../blocks/services/widgets.service';

@Component({
  selector: 'app-meter',
  templateUrl: './meter.component.html',
  styleUrls: ['./meter.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class MeterComponent implements OnInit, OnDestroy {
  @ViewChild('meter', { static: true }) meter: ElementRef;
  @ViewChild('chartWrapper') chartWrapper?: ElementRef;
  @Input() widget: MeterWidget;
  @Input() onResizeNotifier: Subject<Widget>;

  loading = true;
  datasets: Array<ChartDataset>;
  data: number[];
  type: ChartType = 'doughnut';
  colors = [{ backgroundColor: ['', ''] }];
  options = {
    rotation: 270,
    circumference: 180,
    responsive: true,
    maintainAspectRatio: false,
    cutout: '70%',
    plugins: {
      meterCenterText: {
        txt1: '',
        txt2: '',
      },
      donutCenterText: false,
      legend: { display: false },
      tooltip: { enabled: false },
      title: {
        display: true,
        color: '#2a2a2a',
        font: {
          weight: 'bold',
          family: '"Open Sans", Arial, sans-serif',
        },
        text: '',
      },
    },
  };

  position: number;
  positionRemainingText: string;
  positionUsedText: string;

  rangeMinimumText = '';
  rangeMaximumText = '';

  private subscriptions = new Subscription();

  @HostListener('window:resize')
  onResize() {
    this.resize();
  }

  constructor(
    private hydraApi: HydraApiService,
    private renderer: Renderer2,
    private delay: DelayService,
    private widgetsService: WidgetsService,
    private toastr: ToastrService,
    private translate: TranslateService
  ) {}

  ngOnInit() {
    this.subscriptions.add(
      this.onResizeNotifier.subscribe((widget: Widget) => {
        if (widget.id === this.widget.id) {
          this.resize();
        }
      })
    );

    this.subscriptions.add(
      this.widgetsService.onReloadData.subscribe((relatedWidget: string) => {
        if (relatedWidget === this.widget.relatedWidgetTopic) {
          this.init();
        }
      })
    );

    this.init();
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  init() {
    this.loading = true;
    this.hydraApi.getWidgetMeterData(
      this.widget.id,
      (mwd: MeterWidgetData) => this.widgetMeterLoaded(mwd),
      (err) => this.widgetMeterFailedToLoad(err)
    );
  }

  widgetMeterLoaded(mwd: MeterWidgetData) {
    this.position = mwd.position;
    this.options.plugins.title.text = this.widget.label;
    this.options.plugins['meterCenterText'].txt1 = mwd.positionRemainingText;
    this.options.plugins['meterCenterText'].txt2 = mwd.positionUsedText;

    this.rangeMinimumText = mwd.rangeMinimumText;
    this.rangeMaximumText = mwd.rangeMaximumText;

    this.datasets = [
      {
        data: this.getDataArray(mwd.rangeMaximum, mwd.position),
        borderWidth: 0,
        backgroundColor: [this.widget.usageColor, this.widget.remainingColor],
      },
    ];

    this.loading = false;
    this.delay.execute(() => this.resize());
  }

  widgetMeterFailedToLoad(err: any) {
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
  }

  getDataArray(max: number, used: number): Array<number> {
    return [used, max - used];
  }

  resize() {
    const parent = this.meter.nativeElement;
    const chartWrapper = this.chartWrapper.nativeElement;
    let wrapperHeight = 0;
    let wrapperWidth = 0;

    if (parent.offsetWidth / 2 > parent.offsetHeight * 0.9) {
      wrapperHeight = parent.offsetHeight * 0.9;
      wrapperWidth = wrapperHeight * 2;
    } else {
      wrapperWidth = parent.offsetWidth;
      wrapperHeight = wrapperWidth / 2;
    }

    this.renderer.setStyle(chartWrapper, 'width', `${wrapperWidth}px`);
    this.renderer.setStyle(chartWrapper, 'height', `${wrapperHeight}px`);
  }
}
