import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  Renderer2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

import { HomePageTab } from '../../../blocks/models/home-page-tab';

@Component({
  selector: 'app-tab-head-item',
  templateUrl: './tab-head-item.component.html',
  styleUrls: ['./tab-head-item.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TabHeadItemComponent implements AfterViewInit, OnChanges {
  @ViewChild('tabHeadItem', { static: true }) tabHeadItem: ElementRef;
  @ViewChild('tabTitle') tabTitle: ElementRef;
  @Input() tab: HomePageTab;
  @Input() isCurrent: boolean;
  @Input() isDeletable: boolean;
  @Input() index: number;
  @Input() editMode: boolean;
  @Output() select = new EventEmitter();
  @Output() delete = new EventEmitter();
  @Output() drop = new EventEmitter();
  constructor(private renderer: Renderer2) {}

  ngAfterViewInit() {
    if (this.isCurrent) {
      this.renderer.addClass(this.tabHeadItem.nativeElement, 'active');
    } else {
      this.renderer.removeClass(this.tabHeadItem.nativeElement, 'active');
    }
  }

  ngOnChanges() {
    if (this.isCurrent) {
      this.renderer.addClass(this.tabHeadItem.nativeElement, 'active');
    } else {
      this.renderer.removeClass(this.tabHeadItem.nativeElement, 'active');
    }
  }

  selectTab() {
    this.select.emit();
  }

  deleteTab(event: Event) {
    event.stopPropagation();

    this.delete.emit();
  }

  onDrop() {
    this.drop.emit();
  }

  adjustTabTitleInputSize() {
    const input = this.tabTitle.nativeElement;
    input.size = input.value.length || 1;
  }
}
