import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { FileSystemFileEntry, NgxFileDropEntry } from 'ngx-file-drop';
import { QuickDropWidget } from '../../../../blocks/models/home-page-tab';
import { SharedDataService } from '../../../../blocks/services/shared-data.service';

@Component({
  selector: 'app-quick-drop',
  templateUrl: './quick-drop.component.html',
  styleUrls: ['./quick-drop.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class QuickDropComponent implements OnInit {
  @Input() widget: QuickDropWidget;

  constructor(private sharedDataService: SharedDataService, private router: Router) {}

  ngOnInit() {}

  dropped(droppedFiles: NgxFileDropEntry[]): void {
    const files = new Array<File>();

    let count = droppedFiles.length;

    for (const droppedFile of droppedFiles) {
      if (!droppedFile.fileEntry.isFile) continue;

      const isLastFile = --count == 0;
      const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;

      fileEntry.file((f) => {
        files.push(f);

        if (isLastFile) {
          this.sharedDataService.files = files;
          this.router.navigateByUrl(this.widget.route);
        }
      });
    }
  }
}
