import { Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';

import { Subscription } from 'rxjs';
import { DelayService } from 'shared-code-lib';
import { Log } from '../../../../blocks/core/log';
import { SummaryCountWidget, WidgetSummary } from '../../../../blocks/models/home-page-tab';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';
import { WidgetsService } from '../../../../blocks/services/widgets.service';

@Component({
  selector: 'app-summary-count',
  templateUrl: './summary-count.component.html',
  styleUrls: ['./summary-count.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SummaryCountComponent implements OnInit, OnDestroy {
  @Input() widget: SummaryCountWidget;

  summary: WidgetSummary = null;
  subscriptions = new Subscription();
  private readonly styles = ['nominal', 'warning', 'error'];
  timer = null;

  loading = false;

  constructor(
    private hydraApi: HydraApiService,
    private delay: DelayService,
    private router: Router,
    private widgetsService: WidgetsService
  ) {}

  ngOnInit() {
    this.load();

    this.subscriptions.add(
      this.widgetsService.onReloadData.subscribe((relatedWidget: string) => {
        if (relatedWidget === this.widget.relatedWidgetTopic) {
          this.load();
        }
      })
    );
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();

    if (this.timer) {
      clearTimeout(this.timer);
    }
  }

  getClassString = (): string => {
    return `count ${this.styles[this.summary.style]}`;
  };

  onClicked = (): void => {
    this.router.navigateByUrl(this.summary.route);
  };

  load = (): void => {
    this.loading = true;
    this.hydraApi.getWidgetSummary(this.widget, this.widgetSummaryLoaded, this.widgetSummaryFailedToLoad);
  };

  widgetSummaryLoaded = (s: WidgetSummary): void => {
    this.summary = s;
    this.loading = false;
  };

  widgetSummaryFailedToLoad = (err: any): void => {
    this.loading = false;
    Log.error('SummaryCountComponent.widgetSummaryFailedToLoad', err);
  };
}
