import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  Renderer2,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
// import 'chartjs-plugin-colorschemes';

import { ChartType } from 'chart.js';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { DonutWidget, DonutWidgetData } from '../../../../blocks/models/home-page-tab';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';

@Component({
  selector: 'app-donut',
  templateUrl: './donut.component.html',
  styleUrls: ['./donut.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DonutComponent implements OnInit, AfterViewInit, OnChanges {
  @ViewChild('chartWrapper', { static: false }) chartWrapper: ElementRef;
  @ViewChild(PerfectScrollbarDirective) scrollbar: PerfectScrollbarDirective;
  @Input() widget: DonutWidget;
  @Input() donutWidgetData: DonutWidgetData;
  @Input() centerLabel: string;
  @Input() selectedSliceBorderColor: string = 'rgba(255,0,0,0.4)';
  @Input() selectedSliceBorderWidth: number = 10;
  @Output() chunkClicked = new EventEmitter<number>();

  loading = true;
  datasets: Array<any>;
  labels: Array<string> = [];
  data: Array<number> = [];
  chartType: ChartType = 'doughnut';
  selectedIndex: number = -1;
  readonly labelMaxCharactersNumber = 25;

  options: any;

  constructor(
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private translate: TranslateService,
    private renderer: Renderer2
  ) {}

  ngOnInit() {
    if (this.donutWidgetData) return;

    this.hydraApi.getWidgetDonutData(
      this.widget.id,
      (result) => this.widgetDonutLoaded(result),
      (err) => this.widgetDonutFailedToLoad(err)
    );
  }

  ngAfterViewInit(): void {
    if (!this.donutWidgetData) return;

    this.widgetDonutLoaded(this.donutWidgetData);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.donutWidgetData && this.donutWidgetData) this.widgetDonutLoaded(this.donutWidgetData);
  }

  widgetDonutLoaded(data: DonutWidgetData) {
    this.data = [];
    this.labels = [];
    const itemsNumber = data.chunks.length;
    const donutConfig = {
      minHeight: 186,
      itemsNumber: 16,
      step: {
        items: 2,
        height: 22,
      },
    };

    if (itemsNumber > donutConfig.itemsNumber) {
      while (itemsNumber > donutConfig.itemsNumber) {
        const { items, height } = donutConfig.step;
        donutConfig.minHeight += height;
        donutConfig.itemsNumber += items;
      }

      if (this.chartWrapper?.nativeElement)
        this.renderer.setStyle(this.chartWrapper.nativeElement, 'min-height', `${donutConfig.minHeight}px`);
    }

    data.chunks.forEach((chunk) => {
      this.data.push(chunk.count);
      this.labels.push(
        chunk.label.length > this.labelMaxCharactersNumber
          ? chunk.label.substring(0, this.labelMaxCharactersNumber) + ' ...'
          : chunk.label
      );
    });

    this.datasets = [
      {
        data: this.data,
        borderWidth: this.selectedIndex == -1 ? 0 : this.selectedSliceBorderWidth,
        borderColor: this.generateBorderColors(),
      },
    ];
    this.setOptions(this.widget ? this.widget.centerLabel : this.centerLabel, data.centerCount);

    this.loading = false;
  }

  setOptions = (centerLabel: string, centerCount): void => {
    this.options = {
      responsive: true,
      maintainAspectRatio: false,
      cutout: '70%',
      plugins: {
        colorschemes: {
          scheme: 'brewer.SetOne7',
          override: true,
        },
        meterCenterText: false,
        donutCenterText: {
          txt1: centerLabel,
          txt2: centerCount.toString(),
        },
        legend: {
          display: true,
          position: 'right',
          fullWidth: true,
          onClick: (e, legendItem) => this.onLegendClick(e, legendItem),
        },
      },
    };
  };

  generateBorderColors = (): string[] => {
    if (this.selectedIndex == -1) return [];

    const borderCOlors = [];
    for (let i = 0; i < this.data.length; i++) {
      if (i == this.selectedIndex) {
        borderCOlors.push(this.selectedSliceBorderColor);
        continue;
      }

      borderCOlors.push('rgba(0,0,0,0)');
    }

    return borderCOlors;
  };

  widgetDonutFailedToLoad(err) {
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
  }

  chartClicked = (event: any): void => {
    if (!event.active || !event.active.length || event.active[0]._index == null) return;
    this.selectedIndex = event.active[0]._index;
    this.chunkClicked.emit(this.selectedIndex);
  };

  onLegendClick = (event, legendItem): void => {
    event.stopPropagation();
    if (!legendItem || legendItem.index == null) return;

    this.selectedIndex = legendItem.index;
    this.chunkClicked.emit(this.selectedIndex);
  };
}
