import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostListener,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';

import { Subscription, interval } from 'rxjs';
import { DelayService, RolePermissions, UserPreferencesService } from 'shared-code-lib';
import { HomePageTab, Widget } from '../../blocks/models/home-page-tab';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { UserStateService } from '../../blocks/services/user-state.service';
import { WidgetsService } from '../../blocks/services/widgets.service';
import { ApiResults, Control, SpinnerState } from './../../blocks/services/models';

declare const $: any;

interface TabsState {
  tabs: Array<HomePageTab>;
  selectedTab: HomePageTab;
}

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DashboardComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('spinnerWrapper', { static: true }) spinnerWrapper: ElementRef;
  @ViewChild('tabsContentScrollbar', { static: true }) tabsContentScrollbar?: PerfectScrollbarDirective;
  @ViewChild('tabsWrapper', { static: true }) tabsWrapper: ElementRef;
  @ViewChild('tabsScrollbar', { static: true }) tabsScrollbar: PerfectScrollbarDirective;
  @HostListener('window:resize')
  onResize() {
    this.calculateItemsWrapperWidth();
  }
  tabs: Array<HomePageTab> = [];
  lastStateOfTabs: TabsState;
  widgets: Array<Widget> = [];
  selectedTab: HomePageTab;
  editMode = false;
  addWidgetModalOpened = false;
  rightControls: Array<Control> = [];
  editControl: Control;
  saveControl: Control;
  cancelControl: Control;
  addWidgetsModal: NgxSmartModalComponent;
  showLoadingIndicator: boolean;
  widgetsAdded: boolean;
  canShowDashboard: boolean;

  workflowPromptsModal: NgxSmartModalComponent;
  showWorkflowPromptsComponent = false;

  subscriptions = new Subscription();

  private readonly preferenceKey = 'home-page-selected-tab-id';
  private readonly defaultTitleArr: Array<string> = ['New', 'Tab'];

  constructor(
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private translate: TranslateService,
    public ngxSmartModalService: NgxSmartModalService,
    private errorsService: ErrorsService,
    private spinnerState: SpinnerStateService,
    private delay: DelayService,
    private userPreferencesService: UserPreferencesService,
    private cdr: ChangeDetectorRef,
    private widgetsService: WidgetsService,
    private userStateService: UserStateService
  ) {}

  ngOnInit() {
    this.canShowDashboard = this.userStateService.hasRolePermission(RolePermissions.HOMEPAGETABS_MANAGE);

    this.editControl = new Control(
      this.enableEditMode,
      this.translate.instant('Common.Edit'),
      'btn btn-primary btn-minwidth',
      null,
      this.translate.instant('Common.Edit'),
      'btn',
      false
    );

    this.saveControl = new Control(
      () => this.saveEdits(false),
      this.translate.instant('Common.Save'),
      'btn btn-primary btn-minwidth',
      null,
      this.translate.instant('Common.Save'),
      'btn',
      false
    );

    this.cancelControl = new Control(
      this.cancelEditing,
      this.translate.instant('Common.Cancel'),
      'btn btn-light btn-minwidth',
      null,
      this.translate.instant('Common.Cancel'),
      'btn',
      false
    );

    this.rightControls.push(this.editControl);

    const defaultTabTitle = this.translate.instant('dashboard.default.tab.name').split(' ');
    this.defaultTitleArr[0] = defaultTabTitle[0];
    this.defaultTitleArr[1] = defaultTabTitle[1];

    if (this.canShowDashboard) this.loadWidgets();
  }

  ngAfterViewInit() {
    this.addWidgetsModal = this.ngxSmartModalService.getModal('addWidgetsModal');

    this.subscriptions.add(
      this.addWidgetsModal.onOpen.subscribe(() => {
        this.addWidgetModalOpened = true;
      })
    );

    this.subscriptions.add(
      this.addWidgetsModal.onClose.subscribe(() => {
        this.addWidgetModalOpened = false;

        if (!this.widgetsAdded) return;

        this.delay.execute(() => {
          this.tabsContentScrollbar.scrollToBottom(0, 300);
        }, 200);
      })
    );

    this.workflowPromptsModal = this.ngxSmartModalService.getModal('workflowPromptsModal');

    this.subscriptions.add(
      this.workflowPromptsModal.onOpen.subscribe(() => (this.showWorkflowPromptsComponent = true))
    );

    this.subscriptions.add(
      this.workflowPromptsModal.onClose.subscribe(() => (this.showWorkflowPromptsComponent = false))
    );
  }

  loadWidgets(): void {
    this.spinnerState.show(this.translate.instant('Common.LoadingData'), this.spinnerWrapper.nativeElement);
    this.hydraApi.getHomePageTabWidgets(this.widgetsLoaded, this.widgetsFailedToLoad);
  }

  getHeaderText(): string {
    return this.editMode ? this.translate.instant('dashboard.home-edit') : this.translate.instant('dashboard.home');
  }

  toggleSpinner(spinnerState: SpinnerState) {
    if (spinnerState.state) {
      this.spinnerState.show(
        spinnerState.message ? spinnerState.message : this.translate.instant('Common.LoadingData'),
        this.spinnerWrapper.nativeElement
      );
    } else {
      this.spinnerState.hide();
    }
  }

  widgetsLoaded = (widgets: Array<Widget>): void => {
    this.calculateItemsWrapperWidth();
    this.widgets = widgets;

    this.manageRefreshIntervalSeconds();

    this.hydraApi.getHomePageTabs(this.homePageTabsLoaded, this.homePageTabsFailedToLoad);
  };

  manageRefreshIntervalSeconds = () => {
    // group widgets by refresh interval
    const groups = this.widgets.reduce((acc, widget) => {
      acc[widget.autoRefreshIntervalSeconds] = [...(acc[widget.autoRefreshIntervalSeconds] || []), widget];
      return acc;
    }, {} as { [key: string]: Array<Widget> });

    for (const key in groups) {
      const autoRefreshIntervalSeconds = +key;
      if (autoRefreshIntervalSeconds > 0) {
        const inteval = interval(autoRefreshIntervalSeconds * 1000);
        this.subscriptions.add(
          inteval.subscribe((_) => {
            this.widgetsService.reloadData(groups[key].map((a) => a.relatedWidgetTopic));
          })
        );
      }
    }
  };

  homePageTabsLoaded = (tabs: Array<HomePageTab>): void => {
    this.tabs = tabs.sort((a: HomePageTab, b: HomePageTab) => a.order - b.order);
    this.spinnerState.hide();
    if (this.tabs.length) {
      const prevSelectedTabId = this.userPreferencesService.getPreference(this.preferenceKey);
      if (prevSelectedTabId) {
        const prevSeletedTab = this.tabs.find((tab) => tab.id === prevSelectedTabId);
        if (prevSeletedTab) {
          this.selectTab(prevSeletedTab);
          return;
        }
      }

      this.selectTab(this.tabs[0]);
    }
  };

  widgetsFailedToLoad = (): void => {
    this.calculateItemsWrapperWidth();
    this.toastr.error(this.translate.instant('dashboard.failed-to-load-widgets'));
    this.spinnerState.hide();
  };

  calculateItemsWrapperWidth(): void {
    const tabsWrapperWidth = this.tabsWrapper.nativeElement.offsetWidth;
    const itemsWrapper: HTMLElement = this.tabsWrapper.nativeElement.querySelector('.items-wrapper');
    itemsWrapper.style.maxWidth = (tabsWrapperWidth / 100) * 80 + 'px';
  }

  homePageTabsFailedToLoad = (): void => {
    this.toastr.error(this.translate.instant('dashboard.failed-to-load-tabs'));
    this.spinnerState.hide();
  };

  selectTab(tab: HomePageTab): void {
    if (this.selectedTab) {
      this.selectedTab.selected = false;
    }

    this.userPreferencesService.setPreference(this.preferenceKey, tab.id);

    tab.selected = true;
    this.selectedTab = tab;

    this.tabsScrollbar.update();
    this.tabsContentScrollbar.update();
    this.tabsContentScrollbar.scrollToTop();
  }

  enableEditMode = (): void => {
    this.rightControls = [];
    this.rightControls.push(this.saveControl, this.cancelControl);
    this.editMode = true;
    this.cdr.markForCheck();

    this.lastStateOfTabs = {
      tabs: this.tabs.map((tab) => new HomePageTab(tab.serializable())),
      selectedTab: this.selectedTab,
    };
  };

  endEditMode = (): void => {
    this.rightControls = [];
    this.rightControls.push(this.editControl);
    this.editMode = false;
    this.cdr.markForCheck();
    this.tabsScrollbar.update();
  };

  cancelEditing = (): void => {
    this.endEditMode();
    this.tabs = [...this.lastStateOfTabs.tabs];

    const lastActiveTab = this.tabs.find((tab) => tab.id === this.lastStateOfTabs.selectedTab.id);

    if (lastActiveTab) {
      this.selectTab(lastActiveTab);
    } else if (this.tabs.length) {
      this.selectTab(this.tabs[0]);
    }

    this.lastStateOfTabs = null;
  };

  saveEdits = (loadingIndicator: boolean): void => {
    if (loadingIndicator) {
      this.showLoadingIndicator = true;
    } else {
      this.spinnerState.show(this.translate.instant('Common.Saving'), this.spinnerWrapper.nativeElement);
    }
    this.tabs.forEach((tab) => {
      if (tab.isNew) {
        tab.id = '';
      }
    });

    this.hydraApi.updateHomePageTabs(
      this.tabs,
      (result: ApiResults) => {
        if (!result.success) {
          this.saveFailed(result);
        } else {
          this.saveSucceeded(result);
        }
      },
      (err: any) => {
        this.toastr.error(this.translate.instant('Common.SavingDateError'));
        this.cancelEditing();
        this.enableEditMode();
        this.spinnerState.hide();
        this.showLoadingIndicator = false;
      }
    );
  };

  saveFailed(result: ApiResults) {
    this.spinnerState.hide();
    this.showLoadingIndicator = false;
    this.tabs = this.tabs.map((tab, i) => {
      if (!tab.id) {
        tab.id = this.getUniqueId();
      }

      return tab;
    });

    this.errorsService.showErrorsModal(result.errors, 'admin-importer.validation-errors');
  }

  saveSucceeded(result: ApiResults) {
    this.endEditMode();
    this.spinnerState.hide();
    this.showLoadingIndicator = false;
  }

  addTab = (): void => {
    const tabId = this.getUniqueId();
    const newTab = new HomePageTab({
      id: tabId,
      order: this.tabs.length,
      title: this.getTabDefaultTitle(),
      items: [],
    });

    newTab.isNew = true;
    this.tabs.push(newTab);

    this.delay.execute(() => {
      this.selectTab(newTab);
      this.tabsScrollbar.scrollToElement(`.${tabId}`, 0, 200);
    }, 0);
  };

  getTabDefaultTitle(): string {
    const titlesArr: Array<string> = [];
    for (let i = 0; i < this.tabs.length; i++) {
      const titleArr = this.tabs[i].title.split(' ');
      if (titleArr[0] === this.defaultTitleArr[0] && titleArr[1] === this.defaultTitleArr[1]) {
        if (titleArr[2]) {
          titlesArr.push(titleArr[2]);
        } else {
          titlesArr.push('0');
        }
      }
    }

    return this.getUniqueTitle(titlesArr);
  }

  getUniqueTitle(array: Array<string>, existingDefaultTitlesNumber?: number): string {
    let titlesNumber = existingDefaultTitlesNumber;

    if (!titlesNumber) {
      titlesNumber = 0;
    }

    if (array.includes(titlesNumber.toString())) {
      return this.getUniqueTitle(array, (titlesNumber += 1));
    } else {
      return this.defaultTitleArr.join(' ') + (titlesNumber ? ` ${titlesNumber}` : '');
    }
  }

  getUniqueId(): string {
    return '_' + (new Date().getUTCMilliseconds().toString() + new Date().getTime().toString()).toString();
  }

  deleteTab = (tab: HomePageTab, index: number): void => {
    if (!tab.items.length) {
      this.confirmedDeleteTab(tab, index);
      return;
    }

    this.ngxSmartModalService.setModalData(
      {
        title: 'MainHeader.DeleteConfirmation',
        text: this.translate.instant('dashboard.confirm-delete-tab'),
        buttonText: this.translate.instant('Common.Delete'),
        confirm: () => {
          this.ngxSmartModalService.getModal('actionConfirmModal').close();
          this.confirmedDeleteTab(tab, index);
        },
        cancel: this.cancelDeleteTab,
      },
      'actionConfirmModal',
      true
    );

    this.ngxSmartModalService.getModal('actionConfirmModal').open();
  };

  confirmedDeleteTab = (tab: HomePageTab, index: number) => {
    this.tabs.splice(index, 1);
    this.reOrderTabs();

    if (tab.selected && this.tabs.length) {
      if (index === this.tabs.length) {
        this.selectTab(this.tabs[this.tabs.length - 1]);
      } else {
        this.selectTab(this.tabs[index]);
      }
    }
  };

  cancelDeleteTab = () => {
    this.ngxSmartModalService.getModal('actionConfirmModal').close();
  };

  reOrderTabs(): Array<HomePageTab> {
    return this.tabs.map((tab: HomePageTab, i: number) => {
      tab.order = i;
      return tab;
    });
  }

  addWidgets(): void {
    if (!this.editMode) {
      this.enableEditMode();
    }

    this.widgetsAdded = false;
    this.addWidgetsModal.open();
  }

  saveWidgetsChanges() {
    if (this.editMode) {
      return;
    }

    this.saveEdits(true);
  }

  getActiveTab(): HomePageTab {
    return this.selectedTab;
  }

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.tabs, event.previousIndex, event.currentIndex);
    this.reOrderTabs();
  }

  closeWorkflowPromptsModal = () => this.workflowPromptsModal.close();

  onWidgetMaximized = (): void => {
    this.tabsContentScrollbar.scrollToTop(0, 0);
    this.tabsContentScrollbar.update();
  };

  onWidgetRestored = (): void => {
    this.tabsContentScrollbar.update();
  };

  onWidgetsAdded = (): void => {
    this.widgetsAdded = true;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
