import { AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { Chart, ChartDataset, ChartOptions, LabelItem } from 'chart.js';

import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { Languages } from 'shared-code-lib';
import { ActivityData, ActivityDataGroupDefinition } from '../../../../blocks/models/activity-data';
import { ActivityWidget } from '../../../../blocks/models/home-page-tab';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';

import { fontString } from 'chart.js/helpers';

@Component({
  selector: 'app-activity',
  templateUrl: './activity.component.html',
  styleUrls: ['./activity.component.scss'],
})
export class ActivityComponent implements OnInit, AfterViewInit {
  @ViewChild('canvasWrapper') canvasWrapper: ElementRef;
  @Input() widget: ActivityWidget;
  public barChartWrapperHeight: number;
  public barChartOptions: ChartOptions;
  public barChartLabels: LabelItem[];
  public barChartData: ChartDataset[];

  private readonly minimumBarHeight = 15;
  private readonly minimumGroupingHeight = 85;
  private readonly widgetHeaderHeight = 40;

  loaded = false;
  hasData = true;
  groupings: ActivityDataGroupDefinition[] = [];
  filtersModal: NgxSmartModalComponent;

  constructor(private hydra: HydraApiService, private ngxSmartModalService: NgxSmartModalService) {}

  ngOnInit(): void {
    this.getData();
  }

  ngAfterViewInit() {
    this.filtersModal = this.ngxSmartModalService.getModal('filtersModal');
  }

  getData(): void {
    this.loaded = false;
    this.hydra.getActivityWidgetData(
      this.widget.id,
      (data) => {
        if (!data.groupings.length) {
          this.hasData = false;
        } else {
          this.initChart(data);
        }

        this.loaded = true;
      },
      (err) => {
        this.loaded = true;
        console.log(err);
      }
    );
  }

  initChart(response: ActivityData) {
    this.groupings = response.groupings;

    const sections = [];
    const subSections = [];
    response.sections.forEach((item) => {
      sections.push(item.text);
      item.subSections.forEach((ss, i) => {
        if (i > 0) sections.push('');

        subSections.push(ss);
      });
    });

    const labels = subSections.map((ss) => ss.text);
    const y1Labels = sections;
    const chartData: Array<ChartDataset> = [];
    let barsCount = 0;

    this.groupings.forEach((grouping) => {
      chartData.push({
        label: grouping.text,
        backgroundColor: grouping.barColor,
        hoverBackgroundColor: grouping.barColor,
        borderColor: grouping.barColor,
        categoryPercentage: this.groupings.length / (this.groupings.length + 1),
        barPercentage: 0.9,
        data: labels.map((x) => 0),
        yAxisID: 'y',
      });
    });

    subSections.forEach((ss, sectionIndex) => {
      ss.groupings.forEach((grouping, i) => {
        const index = chartData.findIndex((x) => x.label == grouping.text);
        if (index == -1) throw new Error(`Invalid Group ${grouping.text}`);

        chartData[index].data[sectionIndex] = grouping.value;
        barsCount += 1;
      });
    });

    const heightBasedOnBars = Math.max(
      subSections.length * this.minimumGroupingHeight,
      subSections.length * chartData.length * this.minimumBarHeight
    );
    const availableHeight = this.widget.minHeight - this.widgetHeaderHeight;

    this.barChartWrapperHeight = availableHeight > heightBasedOnBars ? availableHeight : heightBasedOnBars;
    this.barChartData = chartData;
    this.barChartLabels = labels;
    let _self = this;
    const categoryOffset = (-1 * (_self.barChartWrapperHeight - 50)) / _self.barChartLabels.length / 2;

    this.barChartOptions = {
      responsive: true,
      maintainAspectRatio: false,
      indexAxis: 'y',
      layout: {
        padding: {
          left: Languages.isRTLLanguage() ? 0 : 75,
          right: Languages.isRTLLanguage() ? 75 : 0,
          top: 0,
          bottom: 0,
        },
      },
      scales: {
        y: {
          position: Languages.isRTLLanguage() ? 'right' : 'left',
          type: 'category',
          display: true,
          afterFit: function (scale) {
            scale.width = 125;
          },
          ticks: {
            display: true,
            callback: function (label): string {
              this.chart.ctx.font = fontString(10, 'normal', Chart.defaults.font.family);
              const res = _self.handleLongTextLabels(this.chart.ctx, labels[label] as string, 80);
              return res.label;
            },
          },
          grid: {
            display: true,
          },
        },
        y1: {
          position: Languages.isRTLLanguage() ? 'right' : 'left',
          type: 'category',
          offset: true,
          afterFit: function (scale) {
            scale.width = 0;
          },
          ticks: {
            display: true,
            padding: 25,
            mirror: true,
            font: {
              size: 14,
              weight: 'bold',
            },
            labelOffset: categoryOffset,
            callback: function (_, index): string {
              this.chart.ctx.font = fontString(14, 'bold', Chart.defaults.font.family);
              const res = _self.handleLongTextLabels(this.chart.ctx, y1Labels[index], 170);
              return res.label;
            },
          },
          grid: {
            display: false,
          },
          border: {
            display: false,
          },
        },
        x: {
          ticks: {
            stepSize: 1,

            mirror: Languages.isRTLLanguage(),
          },
          grid: {
            display: false,
          },
        },
      },
      animation: {
        easing: 'easeOutBack',
        duration: 1000,
        onComplete: _self.drawValues,
        onProgress: _self.drawValues,
      },
      plugins: {
        tooltip: {
          bodyAlign: Languages.isRTLLanguage() ? 'right' : 'left',
          titleAlign: Languages.isRTLLanguage() ? 'right' : 'left',
          footerAlign: Languages.isRTLLanguage() ? 'right' : 'left',
        },
        legend: {
          display: false,
        },
      },
    };
  }

  drawValues(chart: any): void {
    const chartInstance: Chart = chart.chart;
    const ctx = chartInstance.ctx;

    chartInstance.data.datasets.forEach((dataset, i) => {
      const meta = chartInstance.getDatasetMeta(i);

      meta.data.forEach((bar, index) => {
        const text = dataset.data[index].toString();
        if (!text) return;

        ctx.font = fontString(10, 'bold', Chart.defaults.font.family);
        ctx.fillStyle = '#fff';
        ctx.textBaseline = 'middle';
        ctx.fillText(text, bar.x, bar.y + 5);
      });
    });
  }

  handleLongTextLabels(
    ctx: any,
    _label: string,
    maxWidth: number,
    shouldBeDotted?: boolean
  ): { label: string; labelWidth: number } {
    let label = _label;
    let labelWidth = 0;

    if (!_label) return { label, labelWidth };

    labelWidth = ctx.measureText(label).width;
    if (labelWidth > maxWidth) {
      label = label.substring(0, label.length - 1);
      return this.handleLongTextLabels(ctx, label, maxWidth, true);
    } else {
      if (shouldBeDotted) {
        label = label.substring(0, label.length - 1).trim();
        label += '...';
      }

      return { label, labelWidth };
    }
  }

  onFilterChanged(filter: any): void {
    this.getData();
  }

  openFiltersModal() {
    this.ngxSmartModalService.setModalData(
      {
        filters: this.widget.filters,
        groupBy: this.widget.groupBy,
        onFinish: () => {
          this.filtersModal.close();
          this.getData();
        },
      },
      'filtersModal',
      true
    );
    this.filtersModal.open();
  }
}
