import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Inject,
  Input,
  OnInit,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { compileFilter, orderBy, SortDescriptor } from '@progress/kendo-data-query';
import { PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import PerfectScrollbar from 'perfect-scrollbar';
import { ObjectIdGenerator, UserPreferencesService } from 'shared-code-lib';
import {
  HomePageTab,
  IWidget,
  ListViewWidget,
  TabItem,
  Widget,
  WidgetType,
} from '../../../blocks/models/home-page-tab';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
declare const $: any;

@Component({
  selector: 'app-add-widgets-modal',
  templateUrl: './add-widgets-modal.component.html',
  styleUrls: ['./add-widgets-modal.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AddWidgetsModalComponent implements OnInit, AfterViewInit {
  @Output() close = new EventEmitter();
  @Output() widgetsAdded = new EventEmitter();
  @Input() tab: HomePageTab;

  gridView: GridDataResult;
  widgets: Array<IWidget> = [];
  filteredWidgets: Array<IWidget> = [];
  addedItems: Array<TabItem> = [];
  widgetType = WidgetType;
  searchTerm: string;
  isLoading = true;
  noRecordsText: string;
  headerTitle: string;
  sort: SortDescriptor[] = [
    {
      field: 'title',
      dir: 'asc',
    },
  ];
  ps1: PerfectScrollbar;

  constructor(
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private translate: TranslateService,
    private ngxSmartModalService: NgxSmartModalService,
    @Inject(PERFECT_SCROLLBAR_CONFIG) private config: PerfectScrollbarConfigInterface,
    private userPreferencesService: UserPreferencesService,
    private _elementRef: ElementRef,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.noRecordsText = this.translate.instant('dashboard.no-widgets-found');

    this.headerTitle = `${this.translate.instant('dashboard.add-widgets-to')} ${this.tab.title}`;

    this.hydraApi.getHomePageTabWidgets(
      (widgets) => {
        this.widgets = widgets;
        this.filteredWidgets = widgets;
        this.loadWidgets();
        if (widgets.length) {
          this.noRecordsText = this.translate.instant('dashboard.no-matching-widgets');
        }
        this.cdr.detectChanges();
        this.ps1?.update();
        this.isLoading = false;
      },
      (err) => {
        this.toastr.error(this.translate.instant('dashboard.failed-to-load-widgets'));
        this.isLoading = false;
      }
    );
  }

  ngAfterViewInit() {
    const config = this.config;

    const element = this._elementRef.nativeElement.querySelector('.k-grid-content');

    this.ps1 = new PerfectScrollbar(element, config);
    this.cdr.detectChanges();

    this.ps1.update();
  }

  loadWidgets(): void {
    this.gridView = {
      data: orderBy(this.filteredWidgets, this.sort),
      total: this.filteredWidgets.length,
    };
  }

  isChecked(widget: Widget) {
    return this.addedItems.find((item) => item.widgetId === widget.id) ? true : false;
  }

  onChange(widget: Widget) {
    const itemIndex = this.addedItems.findIndex((item) => item.widgetId === widget.id);

    if (itemIndex > -1) {
      this.removeItem(itemIndex);
    } else {
      this.addNewTabItem(widget);
    }
  }

  removeItem(index: number) {
    for (let i = index; i < this.addedItems.length; i++) {
      this.addedItems[i].row -= 1;
    }

    this.addedItems.splice(index, 1);
  }

  addNewTabItem(widget: Widget) {
    const primaryColumn = 0;
    const column = 0;
    let row = 0;
    let tabItem: TabItem;

    if (!this.addedItems.length) {
      this.tab.items.forEach((item) => {
        if (item.primaryColumn === primaryColumn) {
          row = item.row + 1;
        }
      });
    } else {
      this.addedItems.forEach((item) => {
        if (item.primaryColumn === primaryColumn) {
          row = item.row + 1;
        }
      });
    }

    tabItem = new TabItem({
      itemId: ObjectIdGenerator.generateNewObjectId(),
      widgetId: widget.id,
      primaryColumn,
      column,
      row,
      height: widget.defaultHeight,
    });

    this.addedItems.push(tabItem);
    try {
      (widget as ListViewWidget).filters?.forEach((filter) => {
        const filterPreferenceKey = `widget-filter-${filter.id.substring(filter.id.lastIndexOf('.') + 1)}-${
          tabItem.itemId
        }`;
        this.userPreferencesService.setPreference(filterPreferenceKey, filter.defaultValue);
      });
    } catch (error) {
      console.log(`Error occured while processing: ${tabItem.itemId} and widgetId: ${tabItem.widgetId}`);
      console.error(error);
    }
  }

  AddItems() {
    this.addedItems.forEach((item) => {
      this.tab.items.push(item);
    });

    this.tab.items = this.tab.items.slice();

    this.widgetsAdded.emit();
    this.closeAddWidgetsModal();
  }

  onCellClick(event: any) {
    if (event.originalEvent.target.parentElement.classList.contains('check-label')) {
      return;
    }

    this.onChange(event.dataItem);
  }

  sortChange(sort: SortDescriptor[]): void {
    this.sort = sort;
    this.loadWidgets();
  }

  change() {
    const predicate = compileFilter({
      logic: 'or',
      filters: [{ field: 'title', operator: 'contains', value: this.searchTerm }],
    });
    this.filteredWidgets = this.widgets.filter(predicate);
    this.loadWidgets();
  }

  closeAddWidgetsModal = () => {
    this.ngxSmartModalService.getModal('addWidgetsModal').close();
  };
}
